import React, { useState } from "react";
import HighlightOffIcon from '@material-ui/icons/HighlightOff';

function Item(props) {

    const [highlightItem,setHighlightItem]= useState(false);

 
    function handleTouch() {
        setHighlightItem(!highlightItem);
    }

    return <div>
        <hr className="list-border"></hr>
        <div className="list-item">
            <button className="delete-icon" onClick={(event)=>props.deleteItem(event,props.itemId)}><HighlightOffIcon/></button>
            <li onClick={handleTouch} style={{backgroundColor:highlightItem?"#76c2EE":"transparent"}}>{props.item}</li>
        </div>
    </div>
}

export default Item;





