import React, {useState} from "react";
import Input from "./Input";
import Item from "./Item";

function App() {





    const [itemsSubmit,setItemSubmit]=useState([]);

    function submitHandler(event,textChange,setTextChange) {
        setItemSubmit(prevValue => [...prevValue, textChange]);
        setTextChange("");
        event.preventDefault();
    }



    function deleteItem(event,itemId){
        setItemSubmit(
            prevValue=>{
                return prevValue.filter(
                    (item,index)=>
                    {
                        return itemId!==index;
                    }
                )
            }
        )
    }



    return <div className="app-container">
        <h1>Todo-List</h1>
        <Input className="items-container" onAdd={submitHandler}/>
        {itemsSubmit.map((item,index) => <Item itemId={index} key={index} item={item} deleteItem={deleteItem}/>)}
    </div>
}

export default App;