import React, { useState } from "react";
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';

function Input(props) {

    const [textChange,setTextChange]=useState("");

    let clearStatus=false;

    function changeHandler(event) {
        setTextChange(event.target.value);
    }

    return <div className="input-container">
        <form autoComplete="on" onSubmit={event=>{props.onAdd(event,textChange,setTextChange);clearStatus=true;console.log(clearStatus);}}>
            <input className="new-item-textfield" type="text" placeholder="Add New Task" value={textChange} required onChange={changeHandler}/>
            <button className="add-item" type="submit"><AddCircleOutlineIcon /></button>
        </form>
    </div>;
}

export default Input;